import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';
import { ActivatedRoute  } from '@angular/router';

@Component({
  selector: 'app-addfarmer',
  templateUrl: './addfarmer.component.html',
  styleUrls: ['./addfarmer.component.css']
})
export class AddfarmerComponent implements OnInit {
  errorMsg: any = 'Unable to Save the Farmer Details, Please Try again.';
  successMsg: any = 'Farmer is Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  routerData : any ;
  editFarmerFlag:boolean = false;
  deleteFarmerFlag:boolean = false;
  farmerDetails = {
    farmerId: "",
    farmerName: '',
    village: '',
    age: '',
    mandal: '',
    gender: '',
    surveyNum: '',
    noOfAcres: '',
    ownLand: '',
    aadharNum: '',
    pan: '',
    accountNum: '',
    bankName: '',
    ifscCode: '',
    cultivableProds: ''
  }
  constructor(private router: ActivatedRoute,private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
    this.routerData = this.router
      .data
      .subscribe(farmer => {
        this.editFarmerFlag = farmer.editFarmerFlag;
        this.deleteFarmerFlag = farmer.deleteFarmerFlag;
      });
  }
  saveFarmer() {
    debugger;
    console.log("Former Details  : "+this.farmerDetails);
    this.kisanPortalService.saveFarmer(this.farmerDetails).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.successflag = true;
        this.clearFarmerData();
        this.successMsg = "Farmer is Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save the Farmer Details, Please Try again.";
      });
  }
  clearFarmerData() {
    this.farmerDetails = {
      farmerId: "",
      farmerName: '',
      village: '',
      age: '',
      mandal: '',
      gender: '',
      surveyNum: '',
      noOfAcres: '',
      ownLand: '',
      aadharNum: '',
      pan: '',
      accountNum: '',
      bankName: '',
      ifscCode: '',
      cultivableProds: ''
    }
  }
  fetchFarmer() {
    this.kisanPortalService.getFarmerById(this.farmerDetails.farmerId).subscribe((data) => {
      this.farmerDetails = data.json();
      console.log(this.farmerDetails);
    });
}
deleteFarmer(){
  this.kisanPortalService.deleteFarmerById(this.farmerDetails.farmerId).subscribe((data) => {
    //this.farmerDetails = data;
    this.successflag = true;
    this.errorFlag = false;
    this.clearFarmerData();
    this.successMsg = "Farmer "+ this.farmerDetails.farmerId +" Deleted Successfully";
    console.log(this.farmerDetails);
  },
  Error => {
    this.successflag = false;
    this.errorFlag = true;
    this.errorMsg = "Unable to Delete User Details, Please Try again.";
  });
}
}
