import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';
import { ActivatedRoute  } from '@angular/router';

@Component({
  selector: 'app-agent',
  templateUrl: './agent.component.html',
  styleUrls: ['./agent.component.css']
})
export class AgentComponent implements OnInit {
  errorMsg: any = 'Unable to Save Agent Details, Please Try again.';
  successMsg: any = 'Agent Details Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  routerData:any='';
  editAgentFlag:boolean = false;
  deleteAgentFlag:boolean = false;
  agentId:string = '';
  constructor(private router: ActivatedRoute, private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
    this.routerData = this.router
      .data
      .subscribe(agent => {
        this.editAgentFlag = agent.editAgentFlag;
        this.deleteAgentFlag = agent.deleteAgentFlag;
      });
  }
  agent = {
    agentName: "",
    agentType: "",
    description: "",
    addressDetails: {
      addressLine1: "",
      addressLine2: "",
      addressLine3: "",
      city: "",
      state: "",
      pin: 0,
      email: "",
      mobileNum: 0,
      altMobileNum: 0,
      residencePhNum: 0,
      officePhNum: 0,
      addressType: ""
    },
    registrationInfo: {
      pan: "",
      tin: "",
      gst: "",
      cst: "",
      licenceNum: ""
    }
  }
  saveAgent() {
    debugger;
    console.log("Agent Data : " + this.agent);
    this.kisanPortalService.saveAgent(this.agent).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.successflag = true;
        this.clearAgentData();
        this.successMsg = "Agent Details are Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save Agent Details, Please Try again.";
      });
  }
  clearAgentData() {
    this.agent = {
      agentName: "",
      agentType: "",
      description: "",
      addressDetails: {
        addressLine1: "",
        addressLine2: "",
        addressLine3: "",
        city: "",
        state: "",
        pin: 0,
        email: "",
        mobileNum: 0,
        altMobileNum: 0,
        residencePhNum: 0,
        officePhNum: 0,
        addressType: ""
      },
      registrationInfo: {
        pan: "",
        tin: "",
        gst: "",
        cst: "",
        licenceNum: ""
      }
    };
  }
  fetchAgent() {
    this.kisanPortalService.getAgentById(this.agentId).subscribe((data) => {
      this.agent = data.json();
      console.log(this.agent);
    });
}
deleteAgent(){
  this.kisanPortalService.deleteAgentById(this.agentId).subscribe((data) => {
    this.successflag = true;
    this.errorFlag = false;
    this.clearAgentData();
    this.successMsg = "Agent "+ this.agentId +" Deleted Successfully";
    console.log(this.agentId);
  },
  Error => {
    this.successflag = false;
    this.errorFlag = true;
    this.errorMsg = "Unable to Delete Agent Details, Please Try again.";
  });
}
}
