import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { environment } from './../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class KisanloginService {
  // kisanLoginUrl = 'http://localhost:8080/Authenticate';
   loginUrl = environment.kisanLoginUrl;
  
  constructor(private http: Http) { }
  kisanLogin(userLoginData) {
    return this.http.post(this.loginUrl,userLoginData).pipe(map(data => data.json()));
  }
}
