import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';
import { ActivatedRoute  } from '@angular/router';

@Component({
  selector: 'app-item-master',
  templateUrl: './item-master.component.html',
  styleUrls: ['./item-master.component.css']
})
export class ItemMasterComponent implements OnInit {
  errorMsg: any = 'Unable to Save Item Details, Please Try again.';
  successMsg: any = 'Item Details Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  routerData:any = '';
  editItemFlag:boolean=false;
  deleteItemFlag:boolean=false;
  itemId:number;
  items = {
    item_name: "",
    item_type: "",
    item_desc: "",
    item_localname: "",
    rate: 0.00,
    unit: "",
    mrp:0,
    discount_rate: 0.00,
    effective_from: "",
    brand: ""
  }
  constructor(private router: ActivatedRoute, private kisanPortalService : KisanPortalService) { }

  ngOnInit() {
    this.routerData = this.router
      .data
      .subscribe(item => {
        this.editItemFlag = item.editItemFlag;
        this.deleteItemFlag = item.deleteItemFlag;
      });
  }
  saveItem(){
    debugger;
    console.log("Item Data : " + this.items);
    this.kisanPortalService.saveItem(this.items).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.successflag = true;
        this.clearItemData();
        this.successMsg = "Item Details are Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save Item Details, Please Try again.";
      });
  }
  clearItemData(){
    this.items = {
      item_name: "",
      item_type: "",
      item_desc: "",
      item_localname: "",
      rate: 0.00,
      unit: "",
      mrp: 0.00,
      discount_rate: 0.00,
      effective_from: "",
      brand: ""
    }
  }
  fetchItem() {
    this.kisanPortalService.getItemById(this.itemId).subscribe((data) => {
      this.items = data.json();
      console.log(this.items);
    });
}
deleteItem(){
  this.kisanPortalService.deleteItemById(this.itemId).subscribe((data) => {
    //this.userDetails = data;
    this.successflag = true;
    this.errorFlag = false;
    this.clearItemData();
    this.successMsg = "Item "+ this.itemId +" Deleted Successfully";
    console.log(this.itemId);
  },
  Error => {
    this.successflag = false;
    this.errorFlag = true;
    this.errorMsg = "Unable to Delete Item Details, Please Try again.";
  });
}
}
