import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { AddTransactionComponent } from './add-transaction/add-transaction.component';
import { KisanDashboardComponent } from './kisan-dashboard/kisan-dashboard.component';
import { AddfarmerComponent } from './addfarmer/addfarmer.component';
import { AdduserComponent } from './adduser/adduser.component';
import { DashboardhomeComponent } from './dashboardhome/dashboardhome.component';
import { AgentComponent } from './agent/agent.component';
import { ItemMasterComponent } from './item-master/item-master.component';
import { StoreLocationComponent } from './store-location/store-location.component';
import { KisanSupplierComponent } from './kisan-supplier/kisan-supplier.component';
import { KisanCustomerComponent } from './kisan-customer/kisan-customer.component';
import { KisanLoginComponent } from './kisan-login/kisan-login.component';

const routes: Routes = [
  {path: '', redirectTo: '/login', pathMatch: 'full' },
  {path: 'login', component: KisanLoginComponent},
  {
    path: 'kisanDashboard', component: KisanDashboardComponent,
    children: [
      {
        path: '',
        component: DashboardhomeComponent
      },
      {
        path: 'addProduct',
        component: AddTransactionComponent
      },
      {
        path: 'editProduct',
        component: AddTransactionComponent,
        data : {editProductFlag :true}
      },
      {
        path: 'deleteProduct',
        component: AddTransactionComponent,
        data : {deleteProductFlag :true}
      },
      {
        path: 'addFarmer',
        component: AddfarmerComponent
      },
      {
        path: 'editFarmer',
        component: AddfarmerComponent,
        data : {editFarmerFlag :true}
      },
      {
        path: 'deleteFarmer',
        component: AddfarmerComponent,
        data : {deleteFarmerFlag :true}
      },
      {
        path: 'addUser',
        component: AdduserComponent
      },
      {
        path: 'editUser',
        component: AdduserComponent,
        data : {editUserFlag :true}
      },
      {
        path: 'deleteUser',
        component: AdduserComponent,
        data : {deleteUserFlag :true}
      },
      {
        path : 'addAgent',
        component:AgentComponent
      },
      
      {
        path: 'editAgent',
        component: AgentComponent,
        data : {editAgentFlag :true}
      },
      {
        path: 'deleteAgent',
        component: AgentComponent,
        data : {deleteAgentFlag :true}
      },
      {
        path : 'addItem',
        component : ItemMasterComponent
      },
      {
        path: 'editItem',
        component: ItemMasterComponent,
        data : {editItemFlag :true}
      },
      {
        path: 'deleteItem',
        component: ItemMasterComponent,
        data : {deleteItemFlag :true}
      },
      {
        path : 'addLocation',
        component : StoreLocationComponent
      },
      {
        path: 'editLocation',
        component: StoreLocationComponent,
        data : {editLocationFlag :true}
      },
      {
        path: 'deleteLocation',
        component: StoreLocationComponent,
        data : {deleteLocationFlag :true}
      },
      {
        path : 'addSupplier',
        component : KisanSupplierComponent
      },
      {
        path: 'editSupplier',
        component: KisanSupplierComponent,
        data : {editSupplierFlag :true}
      },
      {
        path: 'deleteSupplier',
        component: KisanSupplierComponent,
        data : {deleteSupplierFlag :true}
      },
      {
        path : 'addCustomer',
        component : KisanCustomerComponent
      },
      {
        path: 'editCustomer',
        component: KisanCustomerComponent,
        data : {editCustomerFlag :true}
      },
      {
        path: 'deleteCustomer',
        component: KisanCustomerComponent,
        data : {deleteCustomerFlag :true}
      }
    ]
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})

export class AppRoutingModule { }
