import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-kisan-customer',
  templateUrl: './kisan-customer.component.html',
  styleUrls: ['./kisan-customer.component.css']
})
export class KisanCustomerComponent implements OnInit {
  errorMsg: any = 'Unable to Save Customer Details, Please Try again.';
  successMsg: any = 'Customer Details Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  routerData: any = '';
  editCustomerFlag: boolean = false;
  deleteCustomerFlag: boolean = false;
  customer = {
    custId: 0,
    custName: "",
    custType: "",
    userId: "",
    vehicle: "",
    deliveryaddressDetails: {
      addressLine1: "",
      addressLine2: "",
      addressLine3: "",
      city: "",
      state: "",
      pin: 0,
      email: "",
      mobileNum: 0,
      altMobileNum: 0,
      residencePhNum: 0,
      officePhNum: 0,
      addressType: ""
    },
    registrationInfo: {
      pan: "",
      tin: "",
      gst: "",
      cst: "",
      licenceNum: ""
    }

  }
  constructor(private router: ActivatedRoute, private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
    this.routerData = this.router
      .data
      .subscribe(customer => {
        this.editCustomerFlag = customer.editCustomerFlag;
        this.deleteCustomerFlag = customer.deleteCustomerFlag;
      });
  }
  saveCustomer() {
    debugger;
    console.log("Customer Data : " + this.customer);
    this.kisanPortalService.saveCustomer(this.customer).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.successflag = true;
        this.clearCustomerData();
        this.successMsg = "Customer Details are Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save Customer Details, Please Try again.";
      });
  }
  clearCustomerData() {
    this.customer = {
      custId: 0,
      custName: "",
      custType: "",
      userId: "",
      vehicle: "",
      deliveryaddressDetails: {
        addressLine1: "",
        addressLine2: "",
        addressLine3: "",
        city: "",
        state: "",
        pin: 0,
        email: "",
        mobileNum: 0,
        altMobileNum: 0,
        residencePhNum: 0,
        officePhNum: 0,
        addressType: ""
      },
      registrationInfo: {
        pan: "",
        tin: "",
        gst: "",
        cst: "",
        licenceNum: ""
      }
  
    }
  }
  fetchCustomer() {
    this.kisanPortalService.getCustomerById(this.customer.custId).subscribe((data) => {
      this.customer = data.json();
      console.log(this.customer);
    });
  }
  deleteCustomer() {
    this.kisanPortalService.deleteCustomerById(this.customer.custId).subscribe((data) => {
      this.successflag = true;
      this.errorFlag = false;
      this.clearCustomerData();
      this.successMsg = "Customer " + this.customer.custId + " Deleted Successfully";
      console.log(this.customer.custId);
    },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Delete Customer Details, Please Try again.";
      });
  }
}
