import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';
import { ActivatedRoute  } from '@angular/router';

@Component({
  selector: 'app-adduser',
  templateUrl: './adduser.component.html',
  styleUrls: ['./adduser.component.css']
})
export class AdduserComponent implements OnInit {
  errorMsg: any = 'Unable to Save the User Details, Please Try again.';
  successMsg: any = 'User is Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  routerData : any;
  editUserFlag : boolean = false;
  deleteUserFlag : boolean = false;
  userDetails = {
    userId: '',
    password: '',
    confirmPassword: '',
    userName: '',
    userRole: '',
    location: ''
  }
  constructor(private router: ActivatedRoute, private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
    this.routerData = this.router
      .data
      .subscribe(user => {
        this.editUserFlag = user.editUserFlag;
        this.deleteUserFlag = user.deleteUserFlag;
      });
  }
  saveUser(){
    console.log(this.userDetails);
    if(this.userDetails.password == this.userDetails.confirmPassword){
    this.kisanPortalService.saveUser(this.userDetails).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.userDetails = {
          userId: '',
          password: '',
          confirmPassword: '',
          userName: '',
          userRole: '',
          location: ''
        }
        this.successflag = true;
        this.successMsg = "User is Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save the User Details, Please Try again.";
      })
    }else{
      this.errorFlag = true;
      this.errorMsg = "Password not matched.";
    }
  }
  fetchUser() {
      this.kisanPortalService.getUserById(this.userDetails.userId).subscribe((data) => {
        this.userDetails = data.json();
        console.log(this.userDetails);
      });
  }
  deleteUser(){
    this.kisanPortalService.deleteUserById(this.userDetails.userId).subscribe((data) => {
      //this.userDetails = data;
      this.successflag = true;
      this.errorFlag = false;
      this.userDetails = {
        userId: '',
        password: '',
        confirmPassword: '',
        userName: '',
        userRole: '',
        location: ''
      }
      this.successMsg = "User "+ this.userDetails.userId +" Deleted Successfully";
      console.log(this.userDetails);
    },
    Error => {
      this.successflag = false;
      this.errorFlag = true;
      this.errorMsg = "Unable to Delete User Details, Please Try again.";
    });
  }
}
